@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #e4e4e4;
  --secondary-color: #3468ff;
  --tertiary-color: #3a3985;
  --grey-color: #aaa;
  --dark-color: #333333;
  --black: #222222;

  --gradient:  linear-gradient(to right,
    #3468ff 0%,
    #3a3985 100%
  );
}

.app {
  min-height: 100vh;
  font-family: 'Lato', sans-serif;
  background-color: #333333;
  background-color: var(--dark-color);
}

.query-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.searchbox {
  display: flex;
  margin-bottom: 10px;
}

#search {
  font-family: inherit;
  height: 40px;
  padding: 5px 10px;
  border: none;
  outline: none;
}

#search-btn {
  background: linear-gradient(to right,
    #3468ff 0%,
    #3a3985 100%
  );
  background: var(--gradient);
  border: none;
  outline: none;
  padding: 5px 15px;
  color: #e4e4e4;
  color: var(--primary-color);
  height: 40px;
  cursor: pointer;
  font-family: inherit;
}

.msg {
  color: #ff4040;
  font-size: 14px;
}

.wrapper {
  max-width: 1100px;
  margin: 0 auto; 
  padding: 1rem;
  min-height: 70vh;
  position: relative;
}

.results {
   display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
}

@media(max-width: 500px) {
  .wrapper {
    padding: 10px 5px;
    width: 100%;
  }

  .searchbox {
    width: 100%;
  }

  #search {
    width: 100%;
  }

}



#header {
	background-color: var(--black);
	padding: 2rem;
	text-align: center;
}

#header h1 {
	color: var(--secondary-color);
	font-size: 3rem;
	margin-bottom: 20px;
}

#header h4 {
	color: var(--primary-color);
	font-size: 17px;
}

@media(max-width: 600px) {
	#header {
		padding: 1.5rem;
	}

	#header h1 {
		font-size: 2.5rem;
		margin-bottom: 10px;
	}

	#header h4 {
		font-size: 15px;
	}
}
.result {
	width: 170px;
	height: 250px;
	background-color: var(--black);
	margin: 10px;
	padding: 5px;
	border-radius: 5px;
	text-align: center;
}

.movie-image {
	width: 100%;
	height: 150px;
	border-radius: 5px;
}

.movie-title {
	color: var(--primary-color);
	height: 48px;
	margin-bottom: 5px;
}

#more-btn {
	cursor: pointer;
	border: none;
	outline: none;
	color: var(--primary-color);
	background: var(--gradient);
	font-family: inherit;
	padding: 5px 10px;
	font-size: 14px;
}

@media(max-width: 500px) {
	.result {
		width: 145px;
		margin: 5px;
	}
}
.movie-wrapper {
	padding: 20px;
	max-width: 1100px;
}

.movie-wrapper a.home-link {
	text-decoration: none;
	color: var(--primary-color);
	background: var(--gradient);
	padding: 8px 15px;
}

.movie {
	display: grid;
	grid-template-columns: repeat(2, 1fr );
	grid-gap: 20px;	
	margin-top: 20px;
}

.movie-poster {
	height: 500px;
	width: 400px;
}

.movie-name {
	color: var(--primary-color);
	font-size: 2rem;
	margin-bottom: 15px;
}

.movie-type {
	font-size: 15px;
	font-weight: 300;
	color: var(--grey-color);
	margin-bottom: 5px;
}

.movie-year {
	color: var(--grey-color);
	font-size: 17px;
	font-weight: 300;
	margin-bottom: 25px;
}

.movie-plot {
	color: var(--primary-color);
	margin-bottom: 20px;
	line-height: 1.3;
	letter-spacing: 1.5;
}

.movie-genre,
 .movie-director,
  .movie-actors,
   .movie-rating {
   	color: var(--grey-color);
   	margin-bottom: 10px;
   }

   .movie-genre strong,
 .movie-director strong,
  .movie-actors strong,
   .movie-rating strong {
   	color: var(--primary-color);
   }

@media(max-width: 600px) {

	.movie-wrapper {
		padding: 10px;
	}

	.movie {
		grid-template-columns: 1fr;
	}

	.movie-poster {
		width: auto;
		height: 400px;
	}

}
#footer {
	padding: 2rem;
	text-align: center;
	background: var(--black);
}

#footer p {
	color: var(--grey-color);
	font-size: 13px;
	margin-bottom: 10px;
}

#footer a {
	text-decoration: none;
	color: var(--secondary-color);
}

#footer a:hover {
	text-decoration: underline;
}
#loader {
	display: flex;
	justify-content: center;
	align-items: center;
	background: linear-gradient(to bottom, #444444 0%, #333333 100%);
	position: absolute;
	top: 0;
	left: -150px;
	width: 100vw;
	height: 100%;
	z-index: 2;
}

#loader > span {
	display: inline-block;
	background-color: var(--secondary-color);
	width: 0;
	height: 0;
	border-radius: 50%;
	margin: 0 8px;
	-webkit-transform: translate3d(0);
	        transform: translate3d(0);
	-webkit-animation: bounce 0.6s alternate infinite;
	        animation: bounce 0.6s alternate infinite;
}

#loader > span:nth-child(2) {
	background-color: var(--tertiary-color);
	-webkit-animation-delay: 0.2s;
	        animation-delay: 0.2s;
}

#loader > span:nth-child(3) {
	-webkit-animation-delay: 0.4s;
	        animation-delay: 0.4s;
}

@-webkit-keyframes bounce {
	to {
		width: 16px;
		height: 16px;
		-webkit-transform: translate3d(0, -16px, 0);
		        transform: translate3d(0, -16px, 0);
	}
}

@keyframes bounce {
	to {
		width: 16px;
		height: 16px;
		-webkit-transform: translate3d(0, -16px, 0);
		        transform: translate3d(0, -16px, 0);
	}
}

@media(max-width: 600px) {
	#loader {
		left: 0px;
	}
}
